// Variables
@import 'variables';

@import url($url_0);

html {
  font-family: $font_0;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: $font_2, $font_3, $font_4, $font_5, $font_0;
  font-weight: 300;
  color: $color_6;
  font-size: 12px;
  line-height: 1.75em;
  font-weight: 400;
  input[type='button'] {
    -webkit-appearance: button;
    cursor: pointer;
  }
  input[disabled] {
    cursor: default;
  }
}

body {
  margin: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: 'liga' on;
}

h6,
.h6,
h5,
.h5,
h4,
.h4,
h3,
.h3,
h2,
.h2,
h1,
.h1 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-weight: 500;
  line-height: 1.2;
}

h1,
.h1 {
  font-size: calc(1.375rem + 1.5vw);
}
@media (min-width: 1200px) {
  h1,
  .h1 {
    font-size: 2.5rem;
  }
}

h2,
.h2 {
  font-size: calc(1.325rem + 0.9vw);
}
@media (min-width: 1200px) {
  h2,
  .h2 {
    font-size: 2rem;
  }
}

h3,
.h3 {
  font-size: calc(1.3rem + 0.6vw);
}
@media (min-width: 1200px) {
  h3,
  .h3 {
    font-size: 1.75rem;
  }
}

h4,
.h4 {
  font-size: calc(1.275rem + 0.3vw);
}
@media (min-width: 1200px) {
  h4,
  .h4 {
    font-size: 1.5rem;
  }
}

h5,
.h5 {
  font-size: 1.25rem;
}

h6,
.h6 {
  font-size: 1rem;
}

a {
  color: $color_6;
  text-decoration: none;
}

label {
  margin-bottom: 6px;
  cursor: pointer;
  color: $color_5;
  font-size: 14px;
  font-weight: 500;
  display: block;
}

select {
  text-transform: none;
  outline: none;
  border: 1px solid $color_12;
  border-radius: 8px;
  padding: 10px 12px;
  margin: 0 auto 1em;
  width: 100%;
  height: 40px;
  background-color: $color_2;
  color: $color_13;
}

p {
  margin-bottom: 1rem;
  margin-top: 0;
  font-size: 16px;
  color: $color_10;
}

ul {
  margin: 0;
  padding: 0;
  margin-bottom: 1.75em;
  padding-left: 1.1em;
}

input {
  color: $color_13;
}
.button {
  color: $color_2;
  background-color: $color_5;
  display: inline-block;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: 12px 80px;
  font-size: 16px;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.buttons {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-top: 40px;
}

.top-title-content {
  background-color: $color_0;
  text-align: center;
  .top-title {
    color: $color_1;
    margin: 0;
    padding: 24px 0;
  }
}
.master {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.step {
  position: relative;
  z-index: 1;
  list-style: none;
  margin-bottom: 0;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: row-reverse;
  -ms-flex-direction: row-reverse;
  flex-direction: row-reverse;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 40px 16px;

  .step__divider {
    background-color: $color_9;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 195px;
    height: 1px;
    &:first-child {
      -webkit-flex: 1 0 auto;
      -ms-flex: 1 0 auto;
      flex: 1 0 auto;
    }
    &:last-child {
      -webkit-flex: 1 0 auto;
      -ms-flex: 1 0 auto;
      flex: 1 0 auto;
    }
  }

  .step__item {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    z-index: 1;
    position: relative;

    .step__icon {
      background-color: $color_2;
      border: 1px solid $color_6;
      font-style: normal;
      width: 40px;
      height: 40px;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-justify-content: center;
      -ms-flex-pack: center;
      justify-content: center;
      -webkit-align-items: center;
      -ms-flex-align: center;
      align-items: center;
      border-radius: 50%;
      color: $color_6;
      font-size: 14px;
      span {
        display: block;
      }
      svg {
        display: none;
      }
    }
    .step__name {
      position: absolute;
      top: 100%;
      left: 50%;
      text-align: center;
      transform: translateX(-50%);
      margin-top: 16px;
      font-size: 16px;
      color: $color_6;
    }
    .step__number{
        &:hover {
            background-color: $color_4;
            color: $color_2;
          }
    }

    &.active {
      .step__name {
        color: $color_1;
      }
      .step__icon {
        color: $color_1;
        border: 1.5px solid $color_1;
        -webkit-transition: all 0.1s ease-in-out;
        -moz-transition: all 0.1s ease-in-out;
        -o-transition: all 0.1s ease-in-out;
        transition: all 0.1s ease-in-out;
      }
    }
    &.active ~ .step__item {
      .step__icon {
        color: $color_4;
        border: 1.5px solid $color_4;
        transition: all 0.1s ease-in-out;
        span {
          display: none;
          font-size: 0;
        }
        svg {
          display: block;
        }
      }
      .step__name{
        color: $color_4;
      }
      .step__number {
        color: $color_1;
        border: 1.5px solid $color_1;
        -webkit-transition: all 0.1s ease-in-out;
        -moz-transition: all 0.1s ease-in-out;
        -o-transition: all 0.1s ease-in-out;
        transition: all 0.1s ease-in-out;
        &:hover {
            background-color: $color_4;
            color: $color_2;
          }
      }
    }
  }
}
//

.main-container {
  background-color: $color_0;
  margin-top: 40px;
  padding: 30px 16px;
  border-radius: 24px;
  .main{
    max-width: 70%;
    margin: 0 auto;
  }
  .header__title {
    color: $color_1;
  }
  .list {
    padding-left: 0;
    list-style: none;
    margin-bottom: 0;
    margin: 20px 0 35px;
    border-radius: 2px;

    .list__item.list__title {
      background: $color_2;
      color: $color_8;
      font-size: 16px;
      font-weight: 500;
      padding: 7px 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 16px;

      &.success {
        svg {
          color: $color_4;
        }
      }
      &.error {
        svg {
          color: $color_24;
        }
      }
      .requirement-status {
        display: flex;
        align-items: center;
        gap: 4px;
      }
    }
  }
  .list__item {
    position: relative;
    overflow: hidden;
    padding: 21px 20px 7px 20px;
    color: $color_5;
    border-bottom: 1px solid $color_7;
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    justify-content: space-between;
    gap: 16px;
    &:first-letter {
      text-transform: uppercase;
    }
    &:first-child {
      border-bottom: none;
    }
    &.success {
      svg {
        color: $color_4;
      }
    }
    &.error {
      svg {
        color: $color_24;
      }
    }
  }
}
.list__item--permissions {
  span {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    gap: 16px;
    font-weight: 500;
    font-size: 16px;
  }
  .permission-status {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    gap: 4px;
  }
  &.success {
    span {
      color: $color_10;
    }
    svg {
      color: $color_5;
    }
  }
  &.error {
    span {
      color: $color_24;
    }
  }
}
.textarea {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;
  font-size: 14px;
  line-height: 25px;
  height: 150px;
  outline: none;
  border: 1px solid $color_23;
  ~ .button {
    margin-bottom: 35px;
  }
}
.text-center {
  text-align: center;
}
.form-control {
  height: 14px;
  width: 100%;
}
.has-error {
  color: $color_24;
  input {
    color: $color_25;
    border: 1px solid $color_26;
  }
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
input[type='text'] {
  outline: none;
  border: 1px solid $color_12;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 8px;
  padding: 10px 12px;
  width: calc(100% - 24px);
  margin: 0 auto 1em;
}
input[type='password'] {
  outline: none;
  border: 1px solid $color_12;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 8px;
  padding: 10px 12px;
  width: calc(100% - 24px);
  margin: 0 auto 1em;
}
input[type='url'] {
  outline: none;
  border: 1px solid $color_12;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 8px;
  padding: 10px 12px;
  width: calc(100% - 24px);
  margin: 0 auto 1em;
}
input[type='number'] {
  outline: none;
  border: 1px solid $color_12;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 8px;
  padding: 10px 12px;
  width: calc(100% - 24px);
  margin: 0 auto 1em;
}
.tabs {
  padding: 0;
  .tab-input {
    display: none;
    &:checked + .tab-label {
      background-color: $color_2;
      color: $color_5;
      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        height: 100%;
        width: 100%;
        left: 0;
        border-bottom: 1px solid $color_11;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
        transition: all 0.3s ease-in-out;
      }
    }
  }
  .tab-label {
    color: $color_6;
    cursor: pointer;
    position: relative;
    padding: 10px 16px;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.1s ease-in-out;
    &:hover {
      color: $color_29;
    }
  }
  .tabs-label {
    color: $color_6;
    cursor: pointer;
    position: relative;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.1s ease-in-out;
  }
  .tabs-wrap {
    clear: both;
  }
  .tab {
    display: none;
    margin-top: 24px;
    > *:last-child {
      margin-bottom: 0;
    }
  }
}
.float-left {
  float: left;
}
.float-right {
  float: right;
}
.buttons-container {
  min-height: 37px;
  margin: 1em 0 0;
}
.block {
  //Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
  box-shadow: 0 3px 1px $color_32;
  input[type='radio'] {
    width: 100%;
    display: none;
    + label {
      background: $color_33;
      color: $color_10;
      padding-top: 5px;
      -webkit-transition: all 0.1s ease-in-out;
      -moz-transition: all 0.1s ease-in-out;
      -o-transition: all 0.1s ease-in-out;
      //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
      transition: all 0.1s ease-in-out;
      &:hover {
        background: $color_34;
        color: $color_10;
        padding-top: 5px;
      }
    }
    &:checked {
      + label {
        background-color: $color_35;
      }
      ~ .info {
        height: 200px;
        overflow-y: auto;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
        transition: all 0.3s ease-in-out;
      }
    }
    ~ .info > div {
      padding-top: 15px;
    }
  }
  label {
    width: 450px;
    max-width: 100%;
    cursor: pointer;
  }
  span {
    font-family: $font_5;
    font-weight: 700;
    display: block;
    padding: 10px 12px 12px 15px;
    margin: 0;
    cursor: pointer;
  }
}
.info {
  color: $color_18;
  width: 100%;
  line-height: 2;
  padding-left: 15px;
  padding-right: 15px;
  display: block;
  overflow: hidden;
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
  transition: 0.3s ease-out;
}
::selection {
  background: $color_18;
  color: $color_10;
}
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px $color_36;
  -webkit-border-radius: 0;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 0;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 0;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 0;
  background: $color_37;
  -webkit-box-shadow: inset 0 0 6px $color_38;
}
.margin-bottom-1 {
  margin-bottom: 1em;
}
.margin-bottom-2 {
  margin-bottom: 1em;
}
.margin-top-1 {
  margin-top: 1em;
}
.margin-top-2 {
  margin-top: 1em;
}
.alert {
  margin: 0 0 10px;
  font-size: 1.1em;
  background-color: $color_22;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 3px;
  padding: 10px;
  position: relative;
  &.alert-danger {
    background: $color_24;
    color: $color_39;
    padding: 10px 20px 15px;
    h4 {
      color: $color_39;
      margin: 0;
    }
    ul {
      margin: 0;
    }
  }
  .close {
    width: 25px;
    height: 25px;
    padding: 0;
    margin: 0;
    //Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
    box-shadow: none;
    border: 2px solid $color_26;
    outline: none;
    float: right;
    //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
    border-radius: 50%;
    position: absolute;
    right: 0;
    top: 0;
    background-color: transparent;
    cursor: pointer;
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.1s ease-in-out;
    &:hover {
      background-color: $color_39;
      color: $color_24;
    }
  }
}

.form-group.has-error {
